	module.exports = {"data":{"allPostTitles":{"edges":[{"node":{"frontmatter":{"title":"Uport Specs","index":0,"type":"content"},"fields":{"slug":"/readme"}}}]},"navCategories":{"edges":[{"node":{"fields":{"slug":"/readme"},"headings":[{"value":"uPort Specs","depth":1},{"value":"Identities","depth":2},{"value":"Identities created using the uPort Mobile App","depth":3},{"value":"Request Flows","depth":2},{"value":"More about request flows","depth":3},{"value":"Request and Response Transports","depth":2},{"value":"Request/Response Transports","depth":3},{"value":"Off-chain Messages","depth":2},{"value":"More about Off-chain Messages","depth":3},{"value":"On-chain Transactions","depth":2},{"value":"More about Signing Ethereum transactions","depth":3},{"value":"uPort PKI","depth":2},{"value":"More about the uPort PKI","depth":3}],"frontmatter":{"category":"reference","index":0}}},{"node":{"fields":{"slug":"/messages/privatechain"},"headings":[{"value":"Private Chain Provisioning Message","depth":1},{"value":"Attributes","depth":2}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/messages/index"},"headings":[{"value":"Off-chain Messages","depth":1},{"value":"JSON Web Token","depth":2},{"value":"Requirements","depth":3},{"value":"JOSE Header","depth":4},{"value":"Attributes","depth":4},{"value":"Signature Verification","depth":3},{"value":"Unsigned Requests","depth":2},{"value":"Standard Unsigned Parameters","depth":3},{"value":"Message types","depth":2}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/messages/sharereq"},"headings":[{"value":"Selective Disclosure Request","depth":1},{"value":"Attributes","depth":4},{"value":"Unsigned Requests","depth":2}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/messages/shareresp"},"headings":[{"value":"Selective Disclosure Response","depth":1},{"value":"Attributes","depth":4}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/messages/verification"},"headings":[{"value":"Verification","depth":1},{"value":"Attributes","depth":2},{"value":"Claims Best Practices","depth":2},{"value":"Use atomic claims","depth":3},{"value":"Bundling multiple claims as one","depth":3},{"value":"Claim Taxonomy","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/flows/privatechain"},"headings":[{"value":"Private Chain Provisioning Flow","depth":1},{"value":"Requirements","depth":2},{"value":"Provisioning Methodology","depth":2},{"value":"Endpoint","depth":2},{"value":"Client Callback","depth":2},{"value":"Successful Response","depth":3},{"value":"Errors","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/flows/selectivedisclosure"},"headings":[{"value":"Selective Disclosure Flow","depth":1},{"value":"Endpoint","depth":2},{"value":"Send Request","depth":2},{"value":"Client Callback","depth":2},{"value":"Successful Response","depth":3},{"value":"Errors","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/flows/tx"},"headings":[{"value":"Ethereum Transaction Flow","depth":1},{"value":"Endpoint","depth":2},{"value":"Addresses","depth":2},{"value":"ERC 67","depth":2},{"value":"Client Callback","depth":2},{"value":"Successful Response","depth":3},{"value":"Errors","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/flows/verification"},"headings":[{"value":"Send Verification Flow","depth":1},{"value":"Endpoint","depth":2},{"value":"Send Verifications","depth":2},{"value":"Client Callback","depth":2},{"value":"Successful Response","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/flows/index"},"headings":[{"value":"Uport Request Flows","depth":1},{"value":"Specific Application flows","depth":2},{"value":"Errors","depth":2}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/rest-apis/fuel-server"},"headings":[{"value":"Transaction Fueling Server","depth":1},{"value":"API Description","depth":2},{"value":"Fuel Token","depth":3},{"value":"Fund address","depth":3},{"value":"Endpoint","depth":4},{"value":"Header","depth":4},{"value":"Body","depth":4},{"value":"Response","depth":4},{"value":"Response data","depth":4}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/rest-apis/relay-server"},"headings":[{"value":"Meta Transaction Relaying Server","depth":1},{"value":"API Description","depth":2},{"value":"Fuel Token","depth":3},{"value":"MetaSignedTx","depth":3},{"value":"Endpoint","depth":4},{"value":"Header","depth":4},{"value":"Body","depth":4},{"value":"Response","depth":4},{"value":"Response data","depth":4}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/transports/index"},"headings":[{"value":"Request/Response Transports","depth":1},{"value":"Requests","depth":2},{"value":"Open URL","depth":3},{"value":"QR Code","depth":3},{"value":"Push Notifications","depth":3},{"value":"Responses","depth":2},{"value":"Redirect callback type","depth":3},{"value":"HTTP post callback type","depth":3},{"value":"Messaging Server","depth":2},{"value":"Preparing callback URL","depth":3},{"value":"Listening for Response","depth":3},{"value":"Endpoint","depth":4},{"value":"Response","depth":4},{"value":"Response data","depth":4},{"value":"Cleanup","depth":3},{"value":"Endpoint","depth":4},{"value":"Response","depth":4},{"value":"Examples","depth":2},{"value":"Mobile Browser Transport","depth":3},{"value":"Desktop Browser Serverless Transport","depth":3},{"value":"Desktop Browser Server Backed Transport","depth":3},{"value":"Push Notification Transport","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/transports/push"},"headings":[{"value":"Push Notification Transport","depth":1},{"value":"Encrypting the request","depth":2},{"value":"Proper encoding of the request","depth":3},{"value":"Encryption of the request","depth":3},{"value":"Encoding the encrypted data","depth":3},{"value":"Sending the request","depth":2},{"value":"Push notification server","depth":3},{"value":"Endpoint","depth":4},{"value":"Headers","depth":4},{"value":"Body","depth":4},{"value":"Response","depth":4},{"value":"Response data","depth":4}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/pki/index"},"headings":[{"value":"uPort PKI","depth":1},{"value":"Purpose","depth":2},{"value":"Creating and Verifying a JWT","depth":2},{"value":"Identity Document","depth":2},{"value":" item","depth":3},{"value":" item","depth":3},{"value":" item","depth":3},{"value":" item","depth":3},{"value":"Verifying a signature","depth":2},{"value":"Resolving the Public Key for ","depth":3},{"value":"Resolving the Public Encryption Key for ","depth":3},{"value":"Registering an Identity Document","depth":2},{"value":"External Accounts (Key Pairs)","depth":3},{"value":"Smart Contract Accounts","depth":3},{"value":"uPort Mobile App Created Identities","depth":3}],"frontmatter":{"category":"reference","index":null}}},{"node":{"fields":{"slug":"/pki/identitydocument"},"headings":[{"value":"Identity Document","depth":1},{"value":"Contents","depth":2},{"value":"Parameters","depth":3}],"frontmatter":{"category":"reference","index":null}}}]},"postBySlug":{"html":"<h1 id=\"off-chain-messages\"><a href=\"#off-chain-messages\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Off-chain Messages</h1>\n<p>Most request and responses are performed privately off-chain between the different parties to a flow.</p>\n<h2 id=\"json-web-token\"><a href=\"#json-web-token\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>JSON Web Token</h2>\n<p>Most off-chain messages consist of signed JWTs (JSON Web Tokens) as defined in <a href=\"https://tools.ietf.org/html/rfc7519\">RFC 7519</a>.</p>\n<h3 id=\"requirements\"><a href=\"#requirements\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Requirements</h3>\n<p>We currently only support signatures using the <a href=\"https://en.bitcoin.it/wiki/Secp256k1\">secp256k1 ECDSA curve</a>, which is also used by both Bitcoin and Ethereum.</p>\n<h4 id=\"jose-header\"><a href=\"#jose-header\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>JOSE Header</h4>\n<p>The <a href=\"https://tools.ietf.org/html/rfc7519#section-5\">JOSE header</a> indicates the signing algorithm used in the JWT. This MUST contain the following:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-json\"><code class=\"language-json\"><span class=\"token punctuation\">{</span><span class=\"token property\">\"typ\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"JWT\"</span><span class=\"token punctuation\">,</span> <span class=\"token property\">\"alg\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"ES256K\"</span><span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<h4 id=\"attributes\"><a href=\"#attributes\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Attributes</h4>\n<p>The JWT spec calls these claims, but we use the term \"claims\" for identity-specific data. So in this document we will call these standard JWT \"claims\" \"attributes\" instead.</p>\n<table>\n<thead>\n<tr>\n<th>Name</th>\n<th>Description</th>\n<th>Required</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><a href=\"https://tools.ietf.org/html/rfc7519#section-4.1.1\"><code>iss</code></a></td>\n<td>The \n<a href=\"https://github.com/uport-project/mnid\">MNID</a>\n of the signing identity</td>\n<td>yes</td>\n</tr>\n<tr>\n<td><a href=\"https://tools.ietf.org/html/rfc7519#section-4.1.2\"><code>sub</code></a></td>\n<td>The \n<a href=\"https://github.com/uport-project/mnid\">MNID</a>\n of the subject of the JWT</td>\n<td>no</td>\n</tr>\n<tr>\n<td><a href=\"https://tools.ietf.org/html/rfc7519#section-4.1.3\"><code>aud</code></a></td>\n<td>The \n<a href=\"https://github.com/uport-project/mnid\">MNID</a>\n or URL of the audience of the JWT. Our libraries or app will not accept any JWT that has someone else as the audience</td>\n<td>no</td>\n</tr>\n<tr>\n<td><a href=\"https://tools.ietf.org/html/rfc7519#section-4.1.6\"><code>iat</code></a></td>\n<td>The time of issuance</td>\n<td>yes</td>\n</tr>\n<tr>\n<td><a href=\"https://tools.ietf.org/html/rfc7519#section-4.1.4\"><code>exp</code></a></td>\n<td>Expiration time of JWT</td>\n<td>no</td>\n</tr>\n</tbody>\n</table>\n<p>Non Standard attributes:</p>\n<table>\n<thead>\n<tr>\n<th>Name</th>\n<th>Description</th>\n<th>Required</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>callback</code></td>\n<td>Callback URL for returning the response to a request</td>\n<td>no</td>\n</tr>\n<tr>\n<td><code>type</code></td>\n<td>Type of Message</td>\n<td>no</td>\n</tr>\n</tbody>\n</table>\n<h3 id=\"signature-verification\"><a href=\"#signature-verification\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Signature Verification</h3>\n<p>Each uPort compatible JWT must be signed using an secp256k1 curve. The public key is resolved for the <code>iss</code> using the <a href=\"/pki/index\">uPort PKI</a>.</p>\n<h2 id=\"unsigned-requests\"><a href=\"#unsigned-requests\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Unsigned Requests</h2>\n<p>Many apps that run 100% in the browser do not have a secure way of signing a request. Therefore we provide unsigned versions of certain requests.</p>\n<h3 id=\"standard-unsigned-parameters\"><a href=\"#standard-unsigned-parameters\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Standard Unsigned Parameters</h3>\n<p>There are certain standardized parameters that are provided using HTTP query params in the request. Some of these are based on parameters in the <a href=\"https://tools.ietf.org/html/rfc6749\">OAuth 2.0 RFC 6749 Spec</a>:</p>\n<table>\n<thead>\n<tr>\n<th>Name</th>\n<th>Description</th>\n<th>Required</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>client_id</code></td>\n<td>The \n<a href=\"https://github.com/uport-project/mnid\">MNID</a>\n of the requesting identity</td>\n<td>no</td>\n</tr>\n<tr>\n<td><code>callback_url</code></td>\n<td>The URL that receives the response</td>\n<td>no</td>\n</tr>\n<tr>\n<td><code>callback_type</code></td>\n<td>Valid values \n<code>post</code>\n or \n<code>redirect</code>\n. Determines if callback should be sent as a HTTP POST or open the link (\n<code>redirect</code>\n). If unspecified the mobile app will attempt to pick the correct one</td>\n<td>no</td>\n</tr>\n<tr>\n<td><code>label</code></td>\n<td>Plain text name of client to be displayed to user</td>\n<td>no</td>\n</tr>\n</tbody>\n</table>\n<h2 id=\"message-types\"><a href=\"#message-types\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Message types</h2>\n<p>There are several standard message types that the uPort mobile app knows how to handle or create:</p>\n<ul>\n<li><strong><a href=\"sharereq\">Selective Disclosure Request</a></strong> for asking private data from a user</li>\n<li><strong><a href=\"shareresp\">Selective Disclosure Response</a></strong> signed by the app as a response to a Selective Disclosure Request</li>\n<li><strong><a href=\"verification\">Verification</a></strong> signed claim by one party about another party</li>\n<li><strong><a href=\"privatechain\">Private Chain Provisioning Message</a></strong> for provisioning an identity on a private Ethereum chain</li>\n</ul>","timeToRead":3,"excerpt":"Off-chain Messages Most request and responses are performed privately off-chain between the different parties to a flow. JSON Web Token Most…","headings":[{"value":"Off-chain Messages","depth":1},{"value":"JSON Web Token","depth":2},{"value":"Requirements","depth":3},{"value":"JOSE Header","depth":4},{"value":"Attributes","depth":4},{"value":"Signature Verification","depth":3},{"value":"Unsigned Requests","depth":2},{"value":"Standard Unsigned Parameters","depth":3},{"value":"Message types","depth":2}],"frontmatter":{"title":"Off-chain Messages","category":"reference","type":"content"},"fields":{"slug":"/messages/index"}},"postByCategory":{"totalCount":1,"edges":[{"node":{"fields":{"slug":"/readme"},"headings":[{"value":"uPort Specs","depth":1},{"value":"Identities","depth":2},{"value":"Identities created using the uPort Mobile App","depth":3},{"value":"Request Flows","depth":2},{"value":"More about request flows","depth":3},{"value":"Request and Response Transports","depth":2},{"value":"Request/Response Transports","depth":3},{"value":"Off-chain Messages","depth":2},{"value":"More about Off-chain Messages","depth":3},{"value":"On-chain Transactions","depth":2},{"value":"More about Signing Ethereum transactions","depth":3},{"value":"uPort PKI","depth":2},{"value":"More about the uPort PKI","depth":3}],"excerpt":"uPort Specs uPort is a platform for user centric identity and communication. The platform currently consists of our mobile app, Ethereum…","timeToRead":2,"frontmatter":{"title":"Uport Specs","category":"reference","index":0,"type":"content"}}}]}},"pathContext":{"slug":"/messages/index"}}

